          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/frontproducts">Áî¢ÂìÅ</router-link>
          </li>
        </ul>
        <span ref="cartIcon" style="position: relative" v-on="cartIconEvents">
          <router-link
            class="navbar-text position-relative"
