              <img
                :src="item.product.imageUrl"
                alt="ÂïÜÂìÅÂúñÁâá"
                class="me-2"
                style="width: 50px; height: 50px; object-fit: cover"
              />
              <div class="flex-grow-1">
                <div class="fw-bold">{{ item.product.title }}</div>
                <div class="text-dark small">Êï∏Èáè: {{ item.qty }}</div>
              </div>
              <button
                class="btn btn-sm btn-outline-danger ms-2"
                @click.stop.prevent="removeCartItem(item.id)"
              >
                &times;
              </button>
            </div>
            <div class="p-2 text-center">
              <router-link to="/user/cart" class="btn btn-sm btn-dark"
                >ÂâçÂæÄË≥ºÁâ©Ëªä</router-link
              >
            </div>
          </div>
        </span>
      </div>
    </div>
  </nav>
  <main class="flex-grow-1 mt-5 pb-5">
    <div class="card bg-dark text-black" id="basketball1">
      <img
        :src="
          require('@/assets/picture/richard-bagan-SmQ2Cku3alc-unsplash.jpg')
        "
        class="card-img"
        alt="Á±ÉÁêÉÂúñÁâá"
      />
      <div class="card-img-overlay">
        <h5 class="card-title">ÈóúÊñºÁ±ÉÁêÉÁòã</h5>
        <p class="card-text">„ÄåÁ±ÉÁêÉÁòã„Äç‰∏çÂè™ÊòØÂêçË©ûÔºåÊòØ‰∏ÄÁ®ÆÁîüÊ¥ªÊÖãÂ∫¶„ÄÇ</p>
        <p class="card-text">ÊàëÂÄëÁõ∏‰ø°Á±ÉÁêÉËÉΩÈÄ£Áµê‰∫∫Ëàá‰∫∫„ÄÅÂüéÂ∏ÇËàáÂ§¢ÊÉ≥„ÄÇ</p>
        <p class="card-text">ÂæûË°óÈ†≠Âà∞ÁêÉÂ†¥ÔºåÂæûÁ¥†‰∫∫Âà∞ËÅ∑Ê•≠Ôºå</p>
        <p class="card-text">ÊàëÂÄëÊîØÊåÅÊØè‰∏Ä‰ΩçÁÇ∫Â§¢ÊÉ≥Âä™ÂäõÁöÑÁêÉÂì°„ÄÇ</p>
        <p class="card-text">Ê≠°ËøéÂä†ÂÖ•ÊàëÂÄëÔºå‰∏ÄËµ∑ÁÇ∫Á±ÉÁêÉËÄåÁòã</p>
      </div>
    </div>
    <div class="card bg-dark text-black" id="basketball2">
      <img
        :src="require('@/assets/picture/ben-hershey-5nk3wSFUWZc-unsplash.jpg')"
        class="card-img"
        alt="Á±ÉÁêÉÁêÜÂøµÂúñ"
      />
      <div class="card-img-overlay">
        <h5 class="card-title">Á±ÉÁêÉÁòã‰ªãÁ¥π</h5>
        <p class="card-text">
          ÊàëÂÄëÁõ∏‰ø°Á±ÉÁêÉÊòØ‰∏ÄÁ®ÆË™ûË®ÄÔºåÁÑ°ÈúÄÁøªË≠ØÔºåÂçªËÉΩË∑®Ë∂äÊñáÂåñËàáÂπ¥ÈΩ°„ÄÇ
        </p>
        <p class="card-text">
          ÁÑ°Ë´ñ‰Ω†ÊòØÂàùÂ≠∏ËÄÖÈÇÑÊòØËÄÅÊâãÔºåÂú®ÈÄôË£°ÈÉΩËÉΩÊâæÂà∞Â±¨ÊñºËá™Â∑±ÁöÑ‰ΩçÁΩÆ„ÄÇ
        </p>
        <p class="card-text">
          ÊàëÂÄëËá¥ÂäõÊñºÊâìÈÄ†‰∏ÄÂÄãÁÜ±Ë°Ä„ÄÅËá™Áî±„ÄÅ‰∏îÂÖ±ËûçÁöÑÁ±ÉÁêÉÊñáÂåñÂúà„ÄÇ
        </p>
      </div>
    </div>
  </main>
  <footer class="footer-fixed bg-dark text-white text-center py-3">
    <p>&copy; 2025 Á±ÉÁêÉÁòã. All rights reserved.</p>
