  }
  main {
    padding-bottom: 120px; /* ‚¨ÖÔ∏è Â¢ûÂä†Â∫ïÈÉ®Á©∫Èñì‰ª•ÈÅøÂÖç footer ÊìãÂà∞ÂÖßÂÆπ */
  }
}
</style>
